import swal from './swal/sweetalert2';

export default class Alert {
    //ToDo: Przerobi okno alertw (wiele instancji, zawijanie wierszy, zmiana kolejnoci)

    static error(sender: any, message: string | Error) {
        if (message instanceof Error)
            message = message.message;
        swal('Bd', message, 'error')
    }

}


